<template>
  <main class="main" v-if="showContent">
    <Nav />
    <About />
    <Aside />
    <Skills />
    <Works />
    <Contact />
  </main>

  <div class="sphere-container" v-else>
    <div class="sphere sphere1"></div>
    <div class="sphere sphere2"></div>
    <div class="sphere sphere3"></div>
  </div>
</template>

<script>
import About from "@/components/About.vue";
import Aside from "@/components/Aside.vue";
import Skills from "@/components/Skills.vue";
import Works from "@/components/Works.vue";
import Contact from "@/components/Contact.vue";
import Nav from "@/components/Nav.vue";

export default {
  components: {
    About,
    Aside,
    Skills,
    Works,
    Contact,
    Nav
  },
  data() {
    return {
      showContent: false,
    };
  },
  mounted() {
    setTimeout(() => {
      this.showContent = true;
    }, 3000);
  },
};
</script>

<style lang="scss" scoped>
.sphere-container {
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
}

.sphere {
  background: radial-gradient(circle at 30% 30%, #edff50, #e2f01e, #f0da19);
  border-radius: 50%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  height: 40px;
  margin: 0 10px;
  width: 40px;
}

.sphere1 {
  animation: sphere 1.2s 0.2s ease-in-out infinite;
}
.sphere2 {
  animation: sphere 1.2s 0.5s ease-in-out infinite;
}
.sphere3 {
  animation: sphere 1.2s 0.8s ease-in-out infinite;
}

@keyframes sphere {
  0% {
    transform: scale(1.3);
    box-shadow: 0 0 0 rgba(0, 0, 0, 0.7);
  }
  50% {
    transform: translate(0, 50px);
    box-shadow: 0 7px 10px rgba(0, 0, 0, 0.5);
  }
  100% {
    transform: scale(1.3);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0);
  }
}
</style>